body,
 div,
 ul,
 h1,
 h2,
 h3,
 h4,
 h5,
 h6,
 p {
   padding: 0;
   margin: 0;
   box-sizing: border-box;
 }

 address {
   font-style: normal;
 }

 a,
 a:focus,
 a:active,
 a:visited,
 a:hover {
   outline: none;
   text-decoration: none;
 }

 ul {
   list-style: none;
 }

 input,
 button,
 textarea {
   font-family: inherit;
   outline: none;
   border: none;
 }

 input:-webkit-autofill {
   -webkit-box-shadow: 0 0 0 1000px $color-white inset !important;
 }

 img {
   vertical-align: top;
 }

